import React from "react";
import { Bar } from "react-chartjs-2";

const BarChart = ({ x, y }) => {
  const labels = x;
  const data = {
    labels: labels,
    datasets: [
      {
        label: "Y",
        backgroundColor: [
          "rgba(54, 162, 235, 0.6)",
          "rgba(75, 192, 192, 0.6)",
          "rgba(153, 102, 255, 0.6)",
        ],
        data: y,
        barPercentage: 1, //    
        categoryPercentage: 1, //    
      },
    ],
  };

  return (
    <div>
      <h2> </h2>
      <Bar
        data={data}
        options={{
          scales: {
            xAxes: [
              {
                ticks: {
                  autoSkip: false, //    
                },
              },
            ],
          },
          title: {
            display: true,
            text: "Bar Chart",
            fontSize: 20,
          },
          legend: {
            display: true,
            position: "right",
          },
        }}
      />
    </div>
  );
};

export default BarChart;
